*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}



.header{
  font-family: 'Alfa Slab One', cursive;
  font-weight: 500;
  color: rgb(165, 164, 164);
  animation-name: hero;
  animation-duration: 4s;
  animation-delay: -2s;
  position: relative;
}

a {
  text-decoration: none;
  color: whitesmoke;
}

a:hover {
  color: rgb(168, 168, 29);
}


.hero-text{
  font-family: 'Pacifico', cursive;
  color:rgb(97, 182, 149)
}

.Hero{
  background: rgb(235, 235, 235);
  height: 100vh;
  width: 100%;
}

.content {
  background: honeydew;
  height: 80vh;
  width: 80%;
  margin: auto;
  
}

