.flower-header, .recipes-header, .health-header{
  font-family: 'Abril Fatface', cursive;
  font-weight: 400;
  color: rgb(44, 44, 44);
}

.flower-img{
  max-height: 40vh;
 object-fit: cover;
}

@media (min-width: 344px) {  
  .header {font-size:1.5rem;} /*1rem = 16px*/
  #logo { width: 60px;
  }
  
}
 
@media (max-width: 530px) {
  .hero-img {
    display: none;
  }
  .plant {margin-left: 20%;}
  .content {height: 70vh;}
  .article {line-height: 1rem;
  font-size: 12px;}
}
/* Medium devices (tablets, 768px and up) The navbar toggle appears at this breakpoint */
@media (min-width: 768px) {  
  .header {font-size: 2rem;} /*1rem = 16px*/
  #logo {width: 80px;}
}
 
/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) { 
  .header {font-size:2.5rem;} /*1rem = 16px*/
  #logo {width: 100px;}
}
 
/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {  
  .header {font-size: 4.5rem;} /*1rem = 16px*/ 
  #logo {width: 120px;}   
}

@media (min-width: 1200px) and (max-width: 1245px) {
  .hero-img {
    display: none;
  }
}

.flowers-page {
  margin-top: 30vh;
}

.card {
  border-radius: 15px;
}

.recipes{
  background: rgb(235, 235, 235);
}

.recipe-img-2 {
  max-height: 67vh;
  object-fit: cover;
}

.recipe-img, .health-img {
  border-radius: 15px;
}

.button {
  background: black;
  color: rgb(197, 196, 196);
  border-radius: 12px;
  padding: 7px 19px;
  font-weight: 500;
  font-family: 'Domine', serif;
}

.button:hover {
  color: rgb(146, 146, 4);
  background: rgb(88, 88, 88);
}

.recipe-button {
  justify-content: center;
}

.recipe-title {
  border-bottom: 1px solid black;
  padding-bottom: 12px;
  margin-top: 17px;
  font-weight: 600;
} 

.hero-img {
  justify-self: center;
  position: absolute;
  left: 40%;
  top: 45vh;
}

#scroll {
 min-width: 100%;
}

.article {
  line-height: 2rem;
  font-weight: 500;
}

.footer {
  background: black;
  height: 200px;
}

.card-text {
  font-size: 0.9rem;
}